import wepy from 'wepy'

/**
 * 
 */
export default class Storage {
  /**
   * 
   * @param key {string} 
   */
  constructor (key) {
    this.key = key
  }

  /**
   *  storage
   * @param {string} value 
   */
  set (value) {
    wepy.setStorageSync(this.key, value)
  }

  /**
   *  storage
   * @returns {string}
   */
  get () {
    return wepy.getStorageSync(this.key) || ''
  }

  /**
   *  storage
   * @returns {string}
   */
  remove () {
    wepy.removeStorageSync(this.key)
  }

  /**
   *  storage
   */
  clear () {
    wepy.clearStorageSync()
  }
}
